// Polyfill NodeList.prototype.forEach()
if (window.NodeList && !NodeList.prototype.forEach) {
    NodeList.prototype.forEach = Array.prototype.forEach;
}

/* Tronque le texte des cards à une hauteur max (fonctionne avec le max-height appliqué en CSS) */
(function () {
    const texts = document.querySelectorAll(".sba-video__description, .sba-home-main__inner__parallax-block__content__text, .sba-services-cards__card__content__text, .sba-services-cards__card__thumbnail > span");

    if (!texts) {
        return;
    }

    const ellipsisText = function (e, etc) {
        const wordArray = e.innerHTML.split(" ");
        while (e.scrollHeight > e.offsetHeight) {
            wordArray.pop();
            e.innerHTML = wordArray.join(" ") + (etc || "...");
        }
    };

    texts.forEach((elem) => {
        ellipsisText(elem);
    });

})();

